/**
 * It represents category data
 */
export class Category{

    private id : string;
    private name : string;

    constructor(category : any){
        if(category==undefined){
            this.id='';
            this.name='';
        }
        else{
            this.id = category.id;
            this.name = category.name;
        }        
    }

    public getId(): string {
        return this.id;
    }
    public setId(value: string) {
        this.id = value;
    }

    public getName(): string {
        return this.name;
    }
    public setName(value: string) {
        this.name = value;
    }
}
