import {Category} from './Category'

/**
 * It represents product data
 */
export class Product{
    private id: string;    
    private name: string;    
    private price: number;    
    private createdAt: Date;    
    private category: Category;    
    private picture: string;    

    constructor(product : any){
        if(product==undefined){
            this.id = '';
            this.name = '';
            this.price = 0.0;
            this.createdAt = new Date();
            this.category = new Category(undefined);
            this.picture = '';
        }else{
            this.id = product.id;
            this.name = product.name;
            try{
                this.price = parseFloat(product.price);
            }
            catch(e){
                this.price = 0.0
            }
            this.createdAt = new Date(product.createdAt);
            this.category = new Category(product.category);
            this.picture = product.picture;
        }        
    }

    public getId(): string {
        return this.id;
    }
    public setId(value: string) {
        this.id = value;
    }
    public getName(): string {
        return this.name;
    }
    public setName(value: string) {
        this.name = value;
    }
    public getPrice(): number {
        return this.price;
    }
    public setPrice(value: number) {
        this.price = value;
    }
    public getCreatedAt(): Date {
        return this.createdAt;
    }
    public setCreatedAt(value: Date) {
        this.createdAt = value;
    }
    public getCategory(): Category {
        return this.category;
    }
    public setCategory(value: Category) {
        this.category = value;
    }
    public getPicture(): string {
        return this.picture;
    }
    public setPicture(value: string) {
        this.picture = value;
    }
}