export class Option{

    public value : string;
    public label : string;

    constructor(option:any){
        if(option==undefined){
            this.value = '0';
            this.label = '';
        }
        else{
            this.value = option.value;
            this.label = option.label;
        }        
    }

    public getValue():string{
        return this.value;
    }
    public getLabel():string{
        return this.label;
    }
}

export let optionDefault : Option = new Option({value:'0',label:''});